import { Component, OnInit,ViewChild } from '@angular/core';
import { ContextMenuComponent, ContextMenuService } from 'ngx-contextmenu';
import { RightclickComponent } from '../rightclick/rightclick.component';

@Component({
  selector: 'app-rightclickservice',
  templateUrl: './rightclickservice.component.html',
  styleUrls: ['./rightclickservice.component.scss']
})
export class RightclickserviceComponent implements OnInit {

  name = 'Angular';
  public item = { name: 'John', otherProperty: 'Foo' };
  @ViewChild(RightclickComponent) rightClick: RightclickComponent;

  constructor(private contextMenuService: ContextMenuService) { }

  public onContextMenu($event: MouseEvent, item: any): void {
    this.contextMenuService.show.next({
      contextMenu: this.rightClick.contextMenu,
      event: $event,
      item: item

    });
    $event.preventDefault();
    $event.stopPropagation();
  }

  ngOnInit(): void {
  }

}
